import React, { useState, useEffect, useRef } from 'react';
import Peer from 'simple-peer';
import { database } from '../firebase';
import { ref, set, onValue, onChildAdded, onChildRemoved, remove, update } from 'firebase/database';
import EmotionAnalysis from './EmotionAnalysis';
import ConversationSummary from './ConversationSummary';
import AvatarView from './AvatarView'; // For NPC Coach
import './VideoChat.css';

// ICE servers for NAT traversal
const iceServers = {
  iceServers: [
    { urls: 'stun:stun.l.google.com:19302' },
    { urls: 'stun:stun1.l.google.com:19302' },
  ]
};

const VideoChat = () => {
  const [localStream, setLocalStream] = useState(null);
  const [peers, setPeers] = useState([]);
  const [roomId, setRoomId] = useState('');
  const [isInRoom, setIsInRoom] = useState(false);
  const [isCopied, setIsCopied] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [isMuted, setIsMuted] = useState(false);
  const [isVideoOff, setIsVideoOff] = useState(false);
  const [connectionStatus] = useState('');
  const [showEmotionAnalysis, setShowEmotionAnalysis] = useState(false);
  const [humeApiKey, setHumeApiKey] = useState('');
  
  // New state variables for summary
  const [showSummary, setShowSummary] = useState(false);
  const [summaryData, setSummaryData] = useState(null);
  const [detectedTopics, setDetectedTopics] = useState([]);
  const [emotionKeywords, setEmotionKeywords] = useState({});

  const userVideoRef = useRef(null);
  const peersRef = useRef([]);
  const roomRef = useRef(null);
  const myPeerIdRef = useRef(null);
  
  // Set a unique user ID when component mounts
  useEffect(() => {
    myPeerIdRef.current = generateUserId();
  }, []);
  
  // Generate a unique user ID
  const generateUserId = () => {
    return 'user_' + Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
  };
  
  // Global refs to store stream data
  const remoteStreamsRef = useRef({});
  
  // Track emotion data for summary
  const emotionDataRef = useRef({
    user1: {
      emotionTrends: [],
      dominantEmotions: {},
      keywords: {}
    },
    user2: {
      emotionTrends: [],
      dominantEmotions: {},
      keywords: {}
    },
    startTime: Date.now(),
    topics: []
  });

  // Initialize media streams
  useEffect(() => {
    let currentStream; 
    
    navigator.mediaDevices.getUserMedia({ 
      video: true, 
      audio: true 
    })
      .then((stream) => {
        setLocalStream(stream);
        currentStream = stream; 
        if (userVideoRef.current) {
          userVideoRef.current.srcObject = stream;
        }
      })
      .catch((error) => {
        console.error('Error accessing media devices:', error);
        setErrorMessage('Could not access camera or microphone. Please check your permissions.');
      });

    return () => {
      if (currentStream) {
        currentStream.getTracks().forEach((track) => track.stop());
      }
      leaveRoom(false);
    };
  }, []);

  // Toggle audio based on isMuted state
  useEffect(() => {
    if (localStream) {
      localStream.getAudioTracks().forEach(track => {
        track.enabled = !isMuted;
      });
    }
  }, [isMuted, localStream]);

  // Toggle video based on isVideoOff state
  useEffect(() => {
    if (localStream) {
      localStream.getVideoTracks().forEach(track => {
        track.enabled = !isVideoOff;
      });
    }
  }, [isVideoOff, localStream]);

  // Monitor room for other user leaving
  useEffect(() => {
    if (isInRoom && roomRef.current) {
      const roomReference = ref(database, `rooms/${roomRef.current}`);
      
      const unsubscribe = onValue(roomReference, (snapshot) => {
        if (!snapshot.exists()) {
          // Room was deleted (probably by host)
          console.log("Room no longer exists");
          
          if (!showSummary) {
            handleRemoteUserLeft();
          }
          return;
        }
        
        const roomData = snapshot.val();
        
        // Check if we're the host or guest
        const isHost = roomData.hostId === myPeerIdRef.current;
        
        // Check if the other user left
        if ((isHost && roomData.guestLeft) || (!isHost && roomData.hostLeft)) {
          console.log("Other user left flag detected:", isHost ? "guest left" : "host left");
          
          if (!showSummary) {
            handleRemoteUserLeft();
          }
        }
      });
      
      // Return cleanup function
      return () => {
        console.log("Cleaning up room monitoring");
        unsubscribe();
      };
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isInRoom, showSummary]);

  // Handler for when the remote user leaves
  const handleRemoteUserLeft = () => {
    console.log("Remote user has left, preparing summary...");
    
    // Small delay to ensure we have latest data
    setTimeout(() => {
      prepareSummaryData();
      // Show notification to the user
      setErrorMessage('The other user has left the conversation. Generating conversation summary...');
    }, 1000);
  };

  // Join an existing room
  const joinRoom = () => {
    if (!localStream) {
      setErrorMessage('Camera and microphone access is required to join a room.');
      return;
    }
    
    if (!roomId.trim()) {
      setErrorMessage('Please enter a room ID.');
      return;
    }
    
    const roomReference = ref(database, `rooms/${roomId}`);
    onValue(roomReference, (snapshot) => {
      const roomData = snapshot.val();
      if (!roomData) {
        setErrorMessage('Room does not exist.');
        return;
      }

      // Check if anyone has left the room
      if (roomData.hostLeft || roomData.guestLeft) {
        setErrorMessage('The conversation has ended. This room is no longer active.');
        return;
      }
      
      roomRef.current = roomId;
      const hostId = roomData.hostId;
      
      // Create a peer instance as the initiator
      const peer = new Peer({
        initiator: true,
        trickle: false,
        stream: localStream,
        config: iceServers
      });
      
      // Set up stream handler BEFORE signaling
      peer.on('stream', (stream) => {
        // Store stream in ref
        remoteStreamsRef.current[hostId] = stream;
        
        // Update peers state with stream
        setPeers(prevPeers => {
          const updatedPeers = prevPeers.map(p => {
            if (p.peerId === hostId) {
              return { ...p, stream };
            }
            return p;
          });
          return updatedPeers;
        });
      });
      
      // Handle connection established
      peer.on('connect', () => {
        console.log("Connection established with host");
      });
      
      // Handle errors
      peer.on('error', (err) => {
        console.error("Peer connection error:", err);
      });
      
      // When we generate a signal, send it to the host
      peer.on('signal', (data) => {
        set(ref(database, `rooms/${roomId}/peers/${myPeerIdRef.current}`), {
          signal: JSON.stringify(data),
          userId: myPeerIdRef.current,
          timestamp: Date.now()
        });
      });
      
      // Create a peer object with empty stream initially
      const peerObj = { peerId: hostId, peer, stream: null };
      
      // Store peer reference
      peersRef.current.push(peerObj);
      setPeers([peerObj]);
      
      // Listen for signal response from host
      const signalRef = ref(database, `rooms/${roomId}/signals/${myPeerIdRef.current}`);
      onValue(signalRef, (snapshot) => {
        const signalData = snapshot.val();
        if (signalData && signalData.signal) {
          try {
            // Process the host's signal
            peer.signal(JSON.parse(signalData.signal));
            setIsInRoom(true);
            
            // Initialize emotion data in Firebase
            initEmotionData(roomId, myPeerIdRef.current);
          } catch (err) {
            console.error("Error processing host signal:", err);
          }
        }
      });
    }, { onlyOnce: true });
  };

  // Create a new room
  const createRoom = () => {
    if (!localStream) {
      setErrorMessage('Camera and microphone access is required to create a room.');
      return;
    }
    
    setErrorMessage('');
    const newRoomId = Math.random().toString(36).substring(2, 9);
    setRoomId(newRoomId);
    roomRef.current = newRoomId;
    
    // Create room in Firebase with left flags initialized to false
    const roomReference = ref(database, `rooms/${newRoomId}`);
    set(roomReference, {
      createdAt: Date.now(),
      hostId: myPeerIdRef.current,
      hostLeft: false,
      guestLeft: false
    });
    
    // Initialize emotion data in Firebase
    initEmotionData(newRoomId, myPeerIdRef.current);
    
    // Listen for peers joining the room
    const peersReference = ref(database, `rooms/${newRoomId}/peers`);
    
    onChildAdded(peersReference, (snapshot) => {
      const peerData = snapshot.val();
      const peerId = snapshot.key;
      
      // Don't connect to yourself
      if (peerId !== myPeerIdRef.current && peerData.signal) {
        console.log("New peer joined:", peerId);
        
        // Create a new peer instance as the receiver
        const peer = new Peer({
          initiator: false,
          trickle: false,
          stream: localStream,
          config: iceServers
        });
        
        // Set up stream listener BEFORE signaling
        peer.on('stream', (stream) => {
          console.log("Received remote stream from peer:", peerId);
          console.log("Stream has tracks:", stream.getTracks().length);
          
          // Store stream in ref to ensure it persists
          remoteStreamsRef.current[peerId] = stream;
          
          // Update peers state with the new stream
          setPeers(prevPeers => {
            const updatedPeers = prevPeers.map(p => {
              if (p.peerId === peerId) {
                return { ...p, stream };
              }
              return p;
            });
            return updatedPeers;
          });
        });
        
        // Handle connection established
        peer.on('connect', () => {
          console.log("Connection established with peer:", peerId);
        });
        
        // When we get a signal, send it back
        peer.on('signal', (data) => {
          console.log("Host created return signal for peer");
          set(ref(database, `rooms/${newRoomId}/signals/${peerId}`), {
            signal: JSON.stringify(data),
            from: myPeerIdRef.current,
            timestamp: Date.now()
          });
        });
        
        // Handle errors
        peer.on('error', (err) => {
          console.error("Peer connection error:", err);
        });
        
        try {
          // Process the incoming signal
          peer.signal(JSON.parse(peerData.signal));
          
          // Store the peer reference with stream
          const peerObj = { peerId, peer, stream: remoteStreamsRef.current[peerId] };
          peersRef.current.push(peerObj);
          
          // Update the state with the new peer
          setPeers(prevPeers => [...prevPeers, peerObj]);
        } catch (err) {
          console.error("Error processing peer signal:", err);
        }
      }
    });
    
    // Listen for peers leaving the room
    onChildRemoved(peersReference, (snapshot) => {
      const peerId = snapshot.key;
      console.log("Peer left:", peerId);
      
      // Clean up the associated peer
      const peerObj = peersRef.current.find(p => p.peerId === peerId);
      if (peerObj && peerObj.peer) {
        peerObj.peer.destroy();
      }
      
      // Remove from our refs
      peersRef.current = peersRef.current.filter(p => p.peerId !== peerId);
      delete remoteStreamsRef.current[peerId];
      
      // Update state
      setPeers(prevPeers => prevPeers.filter(p => p.peerId !== peerId));
    });
    
    setIsInRoom(true);
  };

  // Initialize emotion data structure in Firebase
  const initEmotionData = (roomId, userId) => {
    const emotionDataRef = ref(database, `emotionData/${roomId}`);
    set(emotionDataRef, {
      startTime: Date.now(),
      topics: [],
      users: {
        [userId]: {
          emotionTrends: [],
          dominantEmotions: {},
          keywords: {}
        }
      }
    });
  };

  // Update emotion data in Firebase
  const updateEmotionData = (data) => {
    if (!data || !data.roomId || !data.userId || !data.summaryData) return;
    
    // Update local summary data ref
    emotionDataRef.current = data.summaryData;
    
    // Update Firebase
    const userEmotionRef = ref(database, `emotionData/${data.roomId}/users/${data.userId}`);
    update(userEmotionRef, {
      emotionTrends: data.summaryData.user1.emotionTrends,
      dominantEmotions: data.summaryData.user1.dominantEmotions,
      keywords: data.summaryData.user1.keywords
    });
    
    // Update topics if any detected
    if (data.summaryData.topics && data.summaryData.topics.length > 0) {
      const topicsRef = ref(database, `emotionData/${data.roomId}/topics`);
      update(topicsRef, data.summaryData.topics);
    }
  };

  // Copy room ID to clipboard
  const copyRoomId = () => {
    navigator.clipboard.writeText(roomId);
    setIsCopied(true);
    setTimeout(() => setIsCopied(false), 2000);
  };

  // Leave room and cleanup connections/data
  const leaveRoom = (showSummaryPage = true) => {
    if (roomRef.current) {
      // If we want to show the summary page, prepare the data
      if (showSummaryPage) {
        prepareSummaryData();
      }
      
      const roomReference = ref(database, `rooms/${roomRef.current}`);
      onValue(roomReference, (snapshot) => {
        const roomData = snapshot.val();
        if (roomData) {
          if (roomData.hostId === myPeerIdRef.current) {
            // We're the host, update the hostLeft flag
            update(roomReference, {
              hostLeft: true,
              leftAt: Date.now()
            }).then(() => {
              // After updating, remove the entire room after a delay
              // This delay gives the other user time to receive the notification
              setTimeout(() => {
                remove(roomReference);
              }, 10000);
            });
          } else {
            // We're a guest, update the guestLeft flag
            update(roomReference, {
              guestLeft: true,
              leftAt: Date.now()
            });
            
            // Also remove our peer data
            remove(ref(database, `rooms/${roomRef.current}/peers/${myPeerIdRef.current}`));
            remove(ref(database, `rooms/${roomRef.current}/signals/${myPeerIdRef.current}`));
          }
        }
      }, { onlyOnce: true });
      
      // Clean up all peer connections
      peersRef.current.forEach(({ peer }) => {
        if (peer) {
          peer.destroy();
        }
      });
      
      // Reset everything
      peersRef.current = [];
      remoteStreamsRef.current = {};
      setPeers([]);
      setIsInRoom(false);
      
      if (!showSummaryPage) {
        window.location.reload();
      }
    }
  };

  // Prepare summary data from Firebase and local data
  const prepareSummaryData = async () => {
    // Prevent duplicate summary preparation
    if (showSummary) {
      console.log("Summary already being shown, skipping preparation");
      return;
    }
  
    console.log("Preparing summary data");
    
    // Fetch the latest emotion data from Firebase
    const emotionDataFirebaseRef = ref(database, `emotionData/${roomRef.current}`);
    onValue(emotionDataFirebaseRef, (snapshot) => {
      const firebaseData = snapshot.val();
      if (firebaseData) {
        // Get all user IDs to collect data
        const userIds = Object.keys(firebaseData.users || {});
        const currentUserId = myPeerIdRef.current;
        const otherUserId = userIds.find(id => id !== currentUserId);
        
        console.log("User IDs:", { currentUserId, otherUserId });
        
        // Build data structure for summary
        const summaryDataObj = {
          startTime: firebaseData.startTime || emotionDataRef.current.startTime,
          topics: firebaseData.topics || emotionDataRef.current.topics || [],
          user1: {
            emotionTrends: [],
            dominantEmotions: {},
            keywords: {}
          },
          user2: {
            emotionTrends: [],
            dominantEmotions: {},
            keywords: {}
          }
        };
        
        // Add current user's data as user1
        if (currentUserId && firebaseData.users[currentUserId]) {
          summaryDataObj.user1 = {
            emotionTrends: firebaseData.users[currentUserId].emotionTrends || [],
            dominantEmotions: firebaseData.users[currentUserId].dominantEmotions || {},
            keywords: firebaseData.users[currentUserId].keywords || {}
          };
        } else {
          // Fallback to local data
          summaryDataObj.user1 = emotionDataRef.current.user1;
        }
        
        // Add other user's data as user2
        if (otherUserId && firebaseData.users[otherUserId]) {
          summaryDataObj.user2 = {
            emotionTrends: firebaseData.users[otherUserId].emotionTrends || [],
            dominantEmotions: firebaseData.users[otherUserId].dominantEmotions || {},
            keywords: firebaseData.users[otherUserId].keywords || {}
          };
        } else {
          // Fallback to local data
          summaryDataObj.user2 = emotionDataRef.current.user2;
        }
        
        console.log("Prepared summary data:", summaryDataObj);
        
        // Set the summary data and show the summary page
        setSummaryData(summaryDataObj);
        setShowSummary(true);
      } else {
        // If no Firebase data, just use local data
        console.log("No Firebase data found, using local data");
        setSummaryData(emotionDataRef.current);
        setShowSummary(true);
      }
    }, { onlyOnce: true });
  };

  // Toggle mute state for audio
  const toggleMute = () => {
    setIsMuted(prev => !prev);
  };

  // Toggle video state
  const toggleVideo = () => {
    setIsVideoOff(prev => !prev);
  };
  
  // Toggle emotion analysis
  const toggleEmotionAnalysis = () => {
    setShowEmotionAnalysis(prev => !prev);
  };
  
  // Setup Hume API Key
  const setHumeKey = (key) => {
    setHumeApiKey(key);
    localStorage.setItem('humeApiKey', key);
  };
  
  // Load Hume API Key from localStorage on component mount
  useEffect(() => {
    // First try environment variable
    const envKey = window.REACT_APP_HUME_API_KEY || 
                   window.REACT_APP_HUME_API_KEY ||
                   window.NEXT_PUBLIC_HUME_API_KEY;
    
    if (envKey) {
      setHumeApiKey(envKey);
    } else {
      // Fall back to localStorage
      const savedKey = localStorage.getItem('humeApiKey');
      if (savedKey) {
        setHumeApiKey(savedKey);
      }
    }
  }, []);

  // Method to get a remote stream for emotion analysis
  const getRemoteStream = () => {
    // Try to find the first peer with a valid stream
    const peerWithStream = peers.find(peer => peer.stream);
    return peerWithStream ? peerWithStream.stream : null;
  };
  
  // Handle closing the summary
  const handleCloseSummary = () => {
    setShowSummary(false);
    window.location.reload();
  };
  
  // Render the remote video component
  const RemoteVideo = ({ peer }) => {
    const videoRef = useRef(null);
    const [connected, setConnected] = useState(false);
    const streamRef = useRef(null);
    const playAttemptRef = useRef(null);
    const mountedRef = useRef(true);
  
    // Clean up function to handle unmounting
    useEffect(() => {
      return () => {
        mountedRef.current = false;
        if (playAttemptRef.current) {
          clearTimeout(playAttemptRef.current);
        }
      };
    }, []);
  
    // Use a separate effect to track the stream
    useEffect(() => {
      // Store the stream in our ref
      streamRef.current = peer.stream;
  
      // Clean up function
      return () => {
        if (playAttemptRef.current) {
          clearTimeout(playAttemptRef.current);
        }
      };
    }, [peer.stream]);
  
    // Use a separate useEffect for video element handling to avoid race conditions
    useEffect(() => {
      const videoElement = videoRef.current;
      const stream = streamRef.current;
      
      // Only proceed if we have both video element and stream
      if (!videoElement || !stream) return;
      
      // Check if stream has changed
      if (videoElement.srcObject !== stream) {
        videoElement.srcObject = stream;
      }
      
      // Define a function to handle playback
      const attemptPlay = () => {
        if (!mountedRef.current) return;
        
        if (videoElement.paused) {
          const playPromise = videoElement.play();
          
          if (playPromise !== undefined) {
            playPromise
              .then(() => {
                if (mountedRef.current) {
                  setConnected(true);
                }
              })
              .catch(err => {
                // Only retry if component is still mounted and error is AbortError
                if (mountedRef.current && err.name === 'AbortError') {
                  // Schedule retry with delay
                  playAttemptRef.current = setTimeout(attemptPlay, 1000);
                }
              });
          }
        } else {
          // Video is already playing
          if (mountedRef.current) {
            setConnected(true);
          }
        }
      };
      
      // Add event listeners to help with playback
      const handleCanPlay = () => {
        attemptPlay();
      };
      
      const handleLoadedMetadata = () => {
        attemptPlay();
      };
      
      // Add event listeners
      videoElement.addEventListener('canplay', handleCanPlay);
      videoElement.addEventListener('loadedmetadata', handleLoadedMetadata);
      
      // Attempt to play right away
      attemptPlay();
      
      // Cleanup function
      return () => {
        videoElement.removeEventListener('canplay', handleCanPlay);
        videoElement.removeEventListener('loadedmetadata', handleLoadedMetadata);
        
        if (playAttemptRef.current) {
          clearTimeout(playAttemptRef.current);
        }
      };
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
  
    return (
        <div className="video-wrapper">
          <video
            ref={videoRef}
            autoPlay
            playsInline
            className="video-player"
            style={{
              width: '100%',
              height: '100%'
            }}
          />
          {!connected && (
            <div className="connection-status">
              {peer.stream ? 'Connecting...' : 'Waiting for stream...'}
            </div>
          )}
        </div>
    );
  };

  return (
    <div className="video-chat-container">
      <h1>XRCupid</h1>
      
      {errorMessage && (
        <div className="error-message">{errorMessage}</div>
      )}

      {/* Summary overlay */}
      {showSummary && (
        <ConversationSummary
          summaryData={summaryData}
          userId={myPeerIdRef.current}
          roomId={roomRef.current}
          onClose={handleCloseSummary}
        />
      )}

      <div className="video-container">
        {/* NPC Coach Avatar */}
        <div className="video-box npc-coach-avatar"> {/* Added a specific class for styling */}
          <h2>NPC Coach</h2>
          <div style={{ width: '100%', height: '240px', border: '1px solid green', position: 'relative', backgroundColor: '#222' }}> 
            <AvatarView />
          </div>
        </div>

        <div className="video-box">
          <h2>You</h2>
          <video
            ref={userVideoRef}
            autoPlay
            muted
            playsInline
            className={`video-player ${isVideoOff ? 'video-off' : ''}`}
          />
          {isVideoOff && (
            <p className="text-small">Camera Off</p>
          )}
        {peers.length === 0 && isInRoom && (
          <div className="video-box empty">
            <h2>Remote User</h2>
            <p className="text-small">Waiting for someone to join...</p>
          </div>
        )}
        </div> {/* Closes the video-box from line 769 */}
      </div> {/* Closes the video-container */}

      {/* Emotion Analysis Section */}
      {showEmotionAnalysis && (
        <div className="emotion-analysis-section">
          {!humeApiKey ? (
            <div className="api-key-form">
              <h3>Enter Hume AI API Key</h3>
              <input
                type="text"
                placeholder="Your Hume AI API Key"
                onChange={(e) => setHumeKey(e.target.value)}
                className="form-control"
              />
              <p className="info-text">
                You need a Hume AI API key to use the emotion analysis feature.
                Get one at <a href="https://hume.ai" target="_blank" rel="noopener noreferrer">hume.ai</a>
              </p>
            </div>
          ) : (
            <EmotionAnalysis
              localStream={localStream}
              remoteStream={getRemoteStream()}
              apiKey={humeApiKey}
              roomId={roomRef.current}
              userId={myPeerIdRef.current}
              onEmotionDataUpdate={updateEmotionData}
            />
          )}
        </div>
      )}

      <div className="controls-container">
        {!isInRoom ? (
          <div className="room-controls">
            <button onClick={createRoom} className="btn-primary">
              Create New Room
            </button>
            <div className="room-join">
              <input
                type="text"
                placeholder="Enter Room ID"
                value={roomId}
                onChange={(e) => setRoomId(e.target.value)}
                className="form-control"
              />
              <button onClick={joinRoom} className="btn-primary">
                Join Room
              </button>
            </div>
          </div>
        ) : (
          <div className="connected-controls">
            {roomId && (
              <div className="room-info">
                <p>
                  Room ID: <span className="room-id-text">{roomId}</span>
                </p>
                <button onClick={copyRoomId} className="btn-secondary">
                  {isCopied ? 'Copied!' : 'Copy Room ID'}
                </button>
                <p className="info-text">
                  Share this ID with someone to join your room
                </p>
              </div>
            )}
            <div className="media-controls">
              <button
                onClick={toggleMute}
                className={`btn-secondary ${isMuted ? 'active' : ''}`}
              >
                {isMuted ? 'Unmute' : 'Mute'}
              </button>
              <button
                onClick={toggleVideo}
                className={`btn-secondary ${isVideoOff ? 'active' : ''}`}
              >
                {isVideoOff ? 'Turn On Camera' : 'Turn Off Camera'}
              </button>
              <button
                onClick={toggleEmotionAnalysis}
                className={`btn-secondary ${showEmotionAnalysis ? 'active' : ''}`}
              >
                {showEmotionAnalysis ? 'Hide Emotions' : 'Show Emotions'}
              </button>
              <button onClick={() => leaveRoom(true)} className="btn-primary">
                Leave Room
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default VideoChat;